/* App.css */

:root {
  --color-background: white;

  --color-background-result: rgb(135, 78, 249);
  --color-background-result-shadow: rgba(92, 25, 226);
  --color-text-result: white;
  --color-summary-bold: rgb(6, 6, 85);
  --color-text-gray: rgba(255, 245, 245, 0.6);
  --color-text-summary: rgb(184, 162, 162);

  --color-background-reaction:rgba(215, 160, 160, 0.14);
  --color-background-memory: rgba(245, 245, 83, 0.14);
  --color-background-verbal: rgba(135, 179, 135, 0.14);
  --color-background-visual: rgba(115, 115, 208, 0.12);
  --color-text-reaction:rgb(203, 99, 99);
  --color-text-memory: rgb(0, 210, 56);
  --color-text-verbal: green;
  --color-text-visual: blue;

  --color-background-btn: rgb(34 34 69 / 88%);

}

html {
  font-size: 10px;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  text-align: center;
  font-family: 'Hanken Grotesk';
}

#result-summary {
  margin: 200px auto;

  width: 500px;
  height: 340px;
  border-radius: 20px;
  background-color: var(--color-background);

  box-shadow: 
  0.5px 0.5px 0px 0px rgb(131 214 255 / 16%), 
  12px 11px 20px 0px rgb(131 170 255 / 12%);

  display: flex;
}

.result-summary-column {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;

  width: 50%;
  padding: 20px 5px;
}

/* result-column */

#result-column {
  background-color: var(--color-background-result);
  box-shadow: inset 0px -170px 140px 20px var(--color-background-result-shadow);
  border-radius: 30px;
  color: var(--color-text-result);
}

.section-title {
  font-size: 1.4rem;
  font-weight: 700;
}

.gray {
  color: var(--color-text-gray);
}

#total-score {
  width: 55%;
  height: 43%;

  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 2px;
  margin-top: -10px;

  background-color: rgba(30, 30, 216, 0.886);
  box-shadow: inset 0px -50px 40px -20px rgba(146, 177, 255, 0.161);

  border-radius: 50%;
}

#result {
  font-size: 4.5rem;
  color: white;
  font-weight: 700;
}

#total-score p:last-child {
  font-size: 1.1rem;
}

#result-column h4 {
  font-size: 2.5rem;
  margin-top: -20px;
}

#result-comment {
  font-size: 1.2rem;
  width: 75%;
}

/* summary column */

#summary-column h5, .score-text {
  color: var(--color-summary-bold) !important;
}

#summary-column h5 {
  width: 100%;
  padding-left: 30px;
  text-align: left;
}

.categories {
  width: 75%;
  height: 11%;
  border-radius: 6px;

  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 8px;

  font-weight: 700;
  color: var(--color-text-summary);
}

.category-name {
  display: flex;
  align-items: center;
  gap: 8px;
}

.icons {
  width: 20%;
}

#Reaction {
  background-color: var(--color-background-reaction);
}

#Reaction .category-name p {
  color: var(--color-text-reaction);
}

#Memory {
  background-color: var(--color-background-memory);
}

#Memory .category-name p {
  color: var(--color-text-memory);
}

#Verbal {
  background-color: var(--color-background-verbal);
}

#Verbal .category-name p {
  color: var(--color-text-verbal);
}

#Visual {
  background-color: var(--color-background-visual);
}

#Visual .category-name p {
  color: var(--color-text-visual);
}

#continue-btn {
  width: 80%;
  height: 12%;
  border-radius: 20px;
  border: none;
  background-color: var(--color-background-btn);
  color: var(--color-background);
  font-weight: 700;
}

#continue-btn:hover {
  background-image: linear-gradient(
    var(--color-background-result),
    var(--color-background-result-shadow)
  );
}

@media only screen and (max-width: 599px) {
  #result-summary, .result-summary-column {
    flex-direction: column;
  }

  #result-summary {
    width: 250px;
    height: 600px;
    margin: 0 auto;
    margin-top: -10px;

  }

  .result-summary-column {
    width: 100%;
    height: 100%;
  }

  #result-column {
    border-radius: 0 0 20px 20px;
  }

}